<template>
    <div class="absolute top-1 right-1">
        <button @click="localSwitcher('fr')" type="button" :class="{'active-lang': lang}" class="btn-local-switcher">FR</button>
        <button @click="localSwitcher('en')" type="button" :class="{'active-lang': !lang}" class="btn-local-switcher">EN</button>
    </div>
</template>

<script>
export default {
    name:"LocalSwitcher",
    data(){
        return {
            lang : true,
        }
    },
    methods:{
        localSwitcher(lang){
            localStorage.setItem("lang", lang);
            this.$i18n.locale = lang;
            if(lang == 'en') {
                document.title = 'Pet Splints - 3D printed animal splint';
                this.lang = false;
            } else {
                document.title = 'Pet Splints - Attelle pour animaux imprimée en 3D';
                this.lang = true;
            }
        },
        checkLangPref() {
            const langLS = localStorage.getItem('lang');
            if (langLS =="fr" || navigator.language == 'fr-FR' || navigator.language == 'fr' || navigator.language == 'fr-CA' || navigator.language == 'fr-CH'){
                this.localSwitcher('fr');
            } else {
                this.localSwitcher('en');
            }
            if (langLS == "en") {
                this.localSwitcher('en');
            }

        },
    },
    beforeMount() {
        this.checkLangPref();
    },
}
</script>

<style lang="scss">
.active-lang {
    background-color: white !important;
    color:#000 !important;
    border-color: #000 !important
}
.btn-local-switcher {
    @apply p-2 border border-black w-10 h-10 m-1 bg-black text-white focus:bg-white focus:text-black focus:border-black hover:bg-white hover:text-black transition-colors duration-300 outline-none shadow;
}
</style>